import React from 'react';
import styles from './Time.module.scss'
export class Time extends React.Component {

    state = {
        time: new Date(),
    }
    themeList = [styles.theme1, styles.theme2, styles.theme3]
    componentDidMount() {
        setInterval(this.update, 1000)
    }

    update = () => {
        this.setState({
            time: new Date()
        })
    };

    render() {
        const h = this.state.time.getHours()
        const m = this.state.time.getMinutes()
        const s = this.state.time.getSeconds()
        const { amPm, separator, theme } = this.props;
        // const getTime = h % 12 + ":" + (m < 10 ? '0' + m : m) + ":" + (s < 10 ? '0' + s : s) + " " + h < 12 ? 'am' : 'pm'
        return (
            <div className={styles.container}>
                <div className={this.themeList[theme]}
                    style={{
                        color: `#${Math.random().toString(16).substr(-6)}`,
                        transition: '1s',
                        borderColor: `#${Math.random().toString(16).substr(-6)}`
                    }}>
                    {amPm ?
                        `${h % 12}${separator}${(m < 10 ? '0' + m : m)}${separator}${(s < 10 ? '0' + s : s)} ${h < 12 ? 'AM' : 'PM'}` :
                        `${h}${separator}${(m < 10 ? '0' + m : m)}${separator}${(s < 10 ? '0' + s : s)}`}
                </div>

            </div >
        )
    }
}