import React from "react";
import styles from "./Clock.module.scss"
import { Time } from "./components/Time";

export class Clock extends React.Component {
    // const onChangeAction = () => {
    //     console.log('yes')
    // }

    state = {
        checked: [true, false],
        separator: ':',
        current: 1,
        randomColor: '#000'
    }

    separatorList = [':', '-', '/', '.', '|']

    changeTheme() {
        this.setState({ randomColor: '#' + Math.random().toString(16).substr(-6) })
        // let randomColor = '#' + Math.random().toString(16).substr(-6);
        // console.log(randomColor)
        if (this.state.current < this.separatorList.length) {
            this.setState({ current: this.state.current + 1 })
            this.setState({ separator: this.separatorList[this.state.current] })
        } else {
            this.setState({ current: 1 })
            this.setState({ separator: this.separatorList[0] })
        }

    }

    checkboxHandler(e) {
        e.target.id === 'format12' ?
            this.setState({
                checked: [e.target.checked, !e.target.checked]
            }) : this.setState({
                checked: [!e.target.checked, e.target.checked]
            })
    }
    renderTime() {
        const amPm = this.state.checked[0]
        const separator = this.state.separator
        const theme = Math.round(Math.random() * 2)
        return <Time {...{ amPm, separator, theme }} />
    }

    render() {
        const timeJsx = this.renderTime()
        return (
            <div>
                <div className={styles.container} onClick={() => this.changeTheme()} style={{ background: this.state.randomColor }}>
                    <h3 className={styles.titleClock}>Click anywhere to change formats clock</h3>
                    <div className={styles.timeFormat}>
                        <span>Time format:</span>
                        <div>
                            <input
                                id='format12'
                                type="checkbox"
                                checked={this.state.checked[0]}
                                onChange={this.checkboxHandler.bind(this)}
                            />
                            <label htmlFor="format12">12</label>
                        </div>
                        <div>
                            <input
                                id='format24'
                                type="checkbox"
                                checked={this.state.checked[1]}
                                onChange={this.checkboxHandler.bind(this)}
                            />
                            <label htmlFor="format24">24</label>
                        </div>


                    </div>
                    <div>{timeJsx}</div>
                </div>

            </div >
        )
    }

}